import React, { useState, useEffect, useCallback } from 'react'
import { motion } from 'framer-motion'
import { useInView } from 'react-intersection-observer'
import { FaCoffee, FaCog, FaChartLine, FaRobot, FaPlus, FaGraduationCap, FaShieldAlt } from 'react-icons/fa'
import { Link } from 'react-router-dom'
import { useSiteSettings } from '@/hooks/useSiteSettings'
import { useAuth } from '@/contexts/AuthContext'
import { experiencesAPI } from '@/services/api'
import { useTranslation } from 'react-i18next'
import { getLocalizedField } from '@/utils/i18nUtils'

// TypeScript Ïù∏ÌÑ∞ÌéòÏù¥Ïä§ Ï†ïÏùò (APIÏùò Experience ÌÉÄÏûÖÍ≥º ÎèôÏùºÌïòÍ≤å)
interface JourneyStep {
  _id?: string
  icon: React.ComponentType<{ className?: string; size?: number }>
  title: string      // ÏßÅÏ±Ö
  company: string    // ÌöåÏÇ¨
  period: string     // Í∏∞Í∞Ñ
  skills: string[]   // Í∏∞Ïà†
  description: string
  order?: number
}

// ÏïÑÏù¥ÏΩò Îß§Ìïë (APIÏóêÏÑú iconKeyÎ•º Î∞õÏùÑ Í≤ΩÏö∞ ÏÇ¨Ïö©)
const iconMap: { [key: string]: React.ComponentType<{ className?: string; size?: number }> } = {
  coffee: FaCoffee,
  FaCoffee: FaCoffee,
  cog: FaCog,
  FaCog: FaCog,
  chart: FaChartLine,
  FaChartLine: FaChartLine,
  robot: FaRobot,
  FaRobot: FaRobot,
  graduation: FaGraduationCap,
  FaGraduationCap: FaGraduationCap,
  shield: FaShieldAlt,
  FaShieldAlt: FaShieldAlt,
}

// Ïú†Ìö®Ìïú ÏïÑÏù¥ÏΩò ÌÇ§ Î™©Î°ù (ÌÉÄÏûÖ ÏïàÏ†ïÏÑ±ÏùÑ ÏúÑÌï¥)
const validIconKeys = Object.keys(iconMap) as Array<keyof typeof iconMap>

// ÏïàÏ†ÑÌïú ÏïÑÏù¥ÏΩò Í∞ÄÏ†∏Ïò§Í∏∞ Ìï®Ïàò
const getSafeIcon = (iconKey: string | undefined): React.ComponentType<{ className?: string; size?: number }> => {
  if (!iconKey || !validIconKeys.includes(iconKey as keyof typeof iconMap)) {
    console.warn(`Invalid icon key: ${iconKey}. Using default icon 'cog'.`)
    return iconMap['cog']!
  }
  return iconMap[iconKey as keyof typeof iconMap]!
}

// Í∏∞Î≥∏ Ïó¨Ï†ï Îç∞Ïù¥ÌÑ∞ (API Î°úÎìú Ïã§Ìå® Ïãú ÏÇ¨Ïö©)
const defaultJourney: JourneyStep[] = [
    {
      icon: FaCoffee,
      company: 'Ïä§ÌÉÄÎ≤ÖÏä§',
      title: 'Î∞îÎ¶¨Ïä§ÌÉÄ',
      period: '2018 - 2023',
      skills: ['Í≥†Í∞ù ÏÑúÎπÑÏä§', 'ÌíàÏßà Í¥ÄÎ¶¨', 'Ìö®Ïú®ÏÑ± Í∞úÏÑ†'],
      description: '5ÎÖÑÍ∞Ñ Í≥†Í∞ù ÏÑúÎπÑÏä§ÏôÄ ÌíàÏßà Í¥ÄÎ¶¨ Í≤ΩÌóò',
      order: 1,
    },
    {
      icon: FaCog,
      company: 'ÏûêÍ∏∞Í∞úÎ∞ú',
      title: 'Í≥µÏû•ÏûêÎèôÌôî ÌïôÏäµ',
      period: '2024 - ÌòÑÏû¨',
      skills: ['PLC', 'IoT', 'ÏûêÎèôÌôî ÏãúÏä§ÌÖú'],
      description: 'PLC, IoT, ÏûêÎèôÌôî ÏãúÏä§ÌÖú ÌïôÏäµ',
      order: 2,
    },
    {
      icon: FaChartLine,
      company: 'ÏûêÍ∏∞Í∞úÎ∞ú',
      title: 'Îç∞Ïù¥ÌÑ∞ Î∂ÑÏÑùÍ∞Ä',
      period: '2024 - ÌòÑÏû¨',
      skills: ['Python', 'ÌÜµÍ≥ÑÌïô', 'Îç∞Ïù¥ÌÑ∞Í≥ºÌïô'],
      description: 'Python, ÌÜµÍ≥ÑÌïô, Îç∞Ïù¥ÌÑ∞Í≥ºÌïô Ï†ÑÎ¨∏Ìôî',
      order: 3,
    },
    {
      icon: FaRobot,
      company: 'ÎØ∏Îûò',
      title: 'ÏûêÎèôÌôî Ï†ÑÎ¨∏Í∞Ä',
      period: '2025 -',
      skills: ['Ïä§ÎßàÌä∏ Ìå©ÌÜ†Î¶¨', 'AI', 'Industry 4.0'],
      description: 'Ïä§ÎßàÌä∏ Ìå©ÌÜ†Î¶¨Ïùò ÎØ∏Îûò Íµ¨ÌòÑ',
      order: 4,
    },
]

const About: React.FC = () => {
  const { settings: siteSettings, loading: settingsLoading } = useSiteSettings()
  const { isAuthenticated } = useAuth()
  const { i18n } = useTranslation()
  const currentLang = i18n.language as 'ko' | 'en' | 'ja'
  const [journey, setJourney] = useState<JourneyStep[]>([])
  const [dataLoading, setDataLoading] = useState(true)
  
  const [ref, inView] = useInView({
    triggerOnce: true,
    threshold: 0.1,
  })

  // Ïó¨Ï†ï Îç∞Ïù¥ÌÑ∞ Î°úÎî© (useCallbackÏúºÎ°ú Î©îÎ™®Ïù¥Ï†úÏù¥ÏÖò)
  const fetchJourneyData = useCallback(async () => {
    try {
      setDataLoading(true)
      const response = await experiencesAPI.getAll()
      
      if (response.data?.data && response.data.data.length > 0) {
        // API Îç∞Ïù¥ÌÑ∞Î•º JourneyStep ÌòïÏãùÏúºÎ°ú Î≥ÄÌôò
        const mappedJourney: JourneyStep[] = response.data.data
          .map((item: any) => ({
            _id: item._id,
            icon: getSafeIcon(item.iconKey), // ÏïàÏ†ÑÌïú ÏïÑÏù¥ÏΩò Í∞ÄÏ†∏Ïò§Í∏∞
            company: item.company || 'ÌöåÏÇ¨Î™Ö ÏóÜÏùå',
            title: item.title,
            period: item.period,
            skills: item.skills || [],
            description: item.description,
            order: item.order || 0,
          }))
          .sort((a: JourneyStep, b: JourneyStep) => (a.order || 0) - (b.order || 0))
        
        setJourney(mappedJourney)
      } else {
        // API Îç∞Ïù¥ÌÑ∞Í∞Ä ÏóÜÏúºÎ©¥ Í∏∞Î≥∏ Îç∞Ïù¥ÌÑ∞ ÏÇ¨Ïö©
        setJourney(defaultJourney)
      }
    } catch (error) {
      console.error('Failed to fetch journey data:', error)
      // Ïò§Î•ò Î∞úÏÉù Ïãú Í∏∞Î≥∏ Îç∞Ïù¥ÌÑ∞ ÏÇ¨Ïö©
      setJourney(defaultJourney)
    } finally {
      setDataLoading(false)
    }
  }, [])

  useEffect(() => {
    fetchJourneyData()
  }, [fetchJourneyData])

  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.2,
      },
    },
  }

  const itemVariants = {
    hidden: { opacity: 0, y: 30 },
    visible: {
      opacity: 1,
      y: 0,
      transition: {
        duration: 0.6,
        ease: 'easeOut',
      },
    },
  }

  // Î°úÎî© ÏÉÅÌÉú
  if (settingsLoading || dataLoading) {
    return (
      <section className="section-padding bg-white dark:bg-dark-800 min-h-screen">
        <div className="max-w-7xl mx-auto flex justify-center py-12">
          <div className="w-12 h-12 border-4 border-primary-600 border-t-transparent rounded-full animate-spin"></div>
        </div>
      </section>
    )
  }

  return (
    <section className="section-padding bg-white dark:bg-dark-800 min-h-screen">
      <div className="max-w-7xl mx-auto">
        <motion.div
          ref={ref}
          variants={containerVariants}
          initial="hidden"
          animate={inView ? 'visible' : 'hidden'}
        >
          {/* 1. Ï†úÎ™© Î∞è Î∂ÄÏ†úÎ™© */}
          <motion.div variants={itemVariants} className="text-center mb-16">
            <h2 className="text-4xl md:text-5xl font-bold text-dark-900 dark:text-white mb-6">
              {getLocalizedField(currentLang, siteSettings?.aboutTitle, siteSettings?.aboutTitleEn, siteSettings?.aboutTitleJa) || 'Ï†ÄÏùò Ïó¨Ï†ï'}
            </h2>
            {(siteSettings?.aboutSubtitle || !settingsLoading) && (
              <p className="text-xl text-dark-600 dark:text-dark-300 max-w-3xl mx-auto mb-8">
                {getLocalizedField(currentLang, siteSettings?.aboutSubtitle, siteSettings?.aboutSubtitleEn, siteSettings?.aboutSubtitleJa) || 'Ïª§Ìîº Ìïú ÏûîÏóêÏÑú ÏãúÏûëÎêú Í≥µÏû•ÏûêÎèôÌôî Ï†ÑÎ¨∏Í∞ÄÎ°úÏùò Ïó¨Ï†ï'}
              </p>
            )}
          </motion.div>

          {/* 2. ÏÜåÍ∞ú Ìé∏Ïßë Î≤ÑÌäº (Ïù∏Ï¶ùÎêú Í≤ΩÏö∞) */}
          {isAuthenticated && (
            <motion.div variants={itemVariants} className="mb-8 flex justify-end">
              <Link
                to="/admin"
                className="inline-flex items-center gap-2 bg-accent-500 hover:bg-accent-600 text-white font-medium px-6 py-3 rounded-lg transition-colors duration-600 shadow-medium hover:shadow-glow"
              >
                <FaPlus /> ÏÜåÍ∞ú Ìé∏Ïßë
              </Link>
            </motion.div>
          )}

          {/* 3. ÏÜåÍ∞ú ÎÇ¥Ïö© */}
          <motion.div variants={itemVariants} className="card p-10 mb-12">
            <h3 className="text-2xl font-bold text-dark-900 dark:text-white mb-6">
              {getLocalizedField(currentLang, siteSettings?.aboutCardTitle, siteSettings?.aboutCardTitleEn, siteSettings?.aboutCardTitleJa) || 'Î∞îÎ¶¨Ïä§ÌÉÄÏóêÏÑú ÏûêÎèôÌôî Ï†ÑÎ¨∏Í∞ÄÎ°ú'}
            </h3>
            <div className="space-y-6 text-dark-600 dark:text-dark-300 leading-relaxed">
              <p className="whitespace-pre-line">
                {getLocalizedField(currentLang, siteSettings?.aboutDescription1, siteSettings?.aboutDescription1En, siteSettings?.aboutDescription1Ja) || 'Ïä§ÌÉÄÎ≤ÖÏä§ÏóêÏÑú 5ÎÖÑÍ∞Ñ Î∞îÎ¶¨Ïä§ÌÉÄÎ°ú ÏùºÌïòÎ©∞ Í≥†Í∞ù ÏÑúÎπÑÏä§, ÌíàÏßà Í¥ÄÎ¶¨, Ìö®Ïú®ÏÑ± Í∞úÏÑ†Ïóê ÎåÄÌïú ÍπäÏùÄ Ïù¥Ìï¥Î•º ÏñªÏóàÏäµÎãàÎã§.'}
              </p>
              <p className="whitespace-pre-line">
                {getLocalizedField(currentLang, siteSettings?.aboutDescription2, siteSettings?.aboutDescription2En, siteSettings?.aboutDescription2Ja) || 'ÌòÑÏû¨Îäî PLC ÌîÑÎ°úÍ∑∏ÎûòÎ∞ç, ÌÜµÍ≥ÑÌïô, Îç∞Ïù¥ÌÑ∞Í≥ºÌïô Îì± Îã§ÏñëÌïú Í∏∞Ïà†ÏùÑ ÌïôÏäµÌïòÎ©∞ Ïä§ÎßàÌä∏ Ìå©ÌÜ†Î¶¨Ïùò ÎØ∏ÎûòÎ•º Ï§ÄÎπÑÌïòÍ≥† ÏûàÏäµÎãàÎã§.'}
              </p>
            </div>

            <div className="mt-8 flex flex-wrap gap-3">
              {(siteSettings?.mainSkills || ['PLC ÌîÑÎ°úÍ∑∏ÎûòÎ∞ç', 'Îç∞Ïù¥ÌÑ∞ Î∂ÑÏÑù', 'Python', 'ÌòëÎèôÎ°úÎ¥á', 'IoT']).map((tech) => (
                <span
                  key={tech}
                  className="px-4 py-2 bg-primary-100 dark:bg-primary-900 text-primary-700 dark:text-primary-300 rounded-full text-sm font-medium"
                >
                  {tech}
                </span>
              ))}
            </div>
          </motion.div>

          {/* 4. ÏÑ±Ïû• Ïó¨Ï†ï */}
          <motion.div variants={itemVariants} className="space-y-6">
            <h3 className="text-2xl font-bold text-dark-900 dark:text-white mb-8 text-center">
              {getLocalizedField(currentLang, siteSettings?.aboutJourneyTitle, siteSettings?.aboutJourneyTitleEn, siteSettings?.aboutJourneyTitleJa) || 'ÏÑ±Ïû• Ïó¨Ï†ï'}
            </h3>
            <div className="grid md:grid-cols-2 gap-6">
              {journey.map((step, index) => {
                const IconComponent = step.icon
                return (
                  <motion.div
                    key={step._id || index}
                    variants={itemVariants}
                    className="card p-6 hover:shadow-xl transition-all duration-300"
                    whileHover={{ y: -5 }}
                  >
                    <div className="flex items-start gap-4">
                      <div className="p-3 bg-primary-100 dark:bg-primary-900 rounded-xl shrink-0">
                        <IconComponent className="text-primary-600 dark:text-primary-400" size={24} />
                      </div>
                      <div className="flex-1">
                        {/* üåü ÌöåÏÇ¨Î™Ö */}
                        <p className="text-xs text-dark-500 dark:text-dark-400 font-medium mb-1">
                          {step.company}
                        </p>
                        {/* üåü ÏßÅÏ±Ö */}
                        <h4 className="text-lg font-bold text-dark-900 dark:text-white mb-1">
                          {step.title}
                        </h4>
                        {/* üåü Í∏∞Í∞Ñ */}
                        <p className="text-sm text-primary-600 dark:text-primary-400 font-medium mb-3">
                          {step.period}
                        </p>
                        {/* üåü Í∏∞Ïà† ÌÉúÍ∑∏ (Í∞ÑÎûµÌïòÍ≤å) */}
                        {step.skills && step.skills.length > 0 && (
                          <div className="flex flex-wrap gap-1.5">
                            {step.skills.slice(0, 4).map((skill, idx) => (
                              <span
                                key={idx}
                                className="px-2 py-0.5 bg-secondary-100 dark:bg-secondary-900/50 text-secondary-700 dark:text-secondary-300 rounded text-xs font-medium"
                              >
                                {skill}
                              </span>
                            ))}
                            {step.skills.length > 4 && (
                              <span className="px-2 py-0.5 text-dark-500 dark:text-dark-400 text-xs">
                                +{step.skills.length - 4}
                              </span>
                            )}
                          </div>
                        )}
                      </div>
                    </div>
                  </motion.div>
                )
              })}
            </div>
          </motion.div>
        </motion.div>
      </div>
    </section>
  )
}

export default About
